@import url(https://use.typekit.net/jys4ekk.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: 'Montserrat',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Start global*/

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html{
	font-size: 10px;
	font-family: 'Montserrat', sans-serif;
	color: #000000;

}

body{
	width: 100%;
	height: 100%;
	background: url(/static/media/sitebg.48aa7db1.jpg) no-repeat center fixed;
	background-size: cover;

}

a{
	text-decoration: none;
	color: #000000;
}

p{
	font-size: 1.8rem;
	font-weight: 300;
}

img{
	width: 100%;
}
/*End global*/

/*Start header*/

.top-nav{
	width: 100%;
	height: 100vh;
	position: fixed;
	top: -100vh;
	z-index: 50;
	background-color: #0f2027;
	border-bottom-right-radius: 100%;
	border-bottom-left-radius: 100%;
	transition: all 650ms cubic-bezier(1,0,0,1);
}

li {
	margin: 0 2rem;
}

.top-nav.open{
	top: 0;
	border-radius: 0;
	border-radius: initial;
}

.dropdown {
	position: relative;
	display: inline-block;
}

.dropdown-content {
	display: none;
	position: absolute;
}

.dropdown:hover .dropdown-content {
	display: block;
}

.menu-toggler{
	position: absolute;
	top: 5rem;
	right: 5rem;
	width: 5rem;
	height: 4rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	cursor: pointer;
	z-index: 1500;
	transition: -webkit-transform 650ms ease-out;
	transition: transform 650ms ease-out;
	transition: transform 650ms ease-out, -webkit-transform 650ms ease-out;
}

.menu-toggler.open{
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}

.bar{
	background: linear-gradient(to right, #203a43, #2c5364);
	width: 100%;
	height: 4px;
	border-radius: .8rem;
}

.bar.half{
	width: 50%;
}

.bar.start{
	-webkit-transform-origin: right;
	        transform-origin: right;
	transition: -webkit-transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
	transition: transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
	transition: transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57), -webkit-transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
}

.open .bar.start{
	-webkit-transform: rotate(-450deg) translateX(.8rem);
	        transform: rotate(-450deg) translateX(.8rem);
}

.bar.end{
	align-self: flex-end;
	-webkit-transform-origin: left;
	        transform-origin: left;
	transition: -webkit-transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
	transition: transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
	transition: transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57), -webkit-transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
}

.open .bar.end{
	-webkit-transform: rotate(-450deg) translateX(-.8rem);
	        transform: rotate(-450deg) translateX(-.8rem);
}

/*End header*/


/*Start animations*/
@-webkit-keyframes pulse{
	0%{
		box-shadow: 0 0 0 0 rgba(15, 32, 39, 0.99);
	}
	70%{
		box-shadow: 0 0 0 2rem rgba(15, 32, 39, 0);
	}
	100%{
		box-shadow: 0 0 0 0 rgba(215, 32, 39, 0);
	}
}
@keyframes pulse{
	0%{
		box-shadow: 0 0 0 0 rgba(15, 32, 39, 0.99);
	}
	70%{
		box-shadow: 0 0 0 2rem rgba(15, 32, 39, 0);
	}
	100%{
		box-shadow: 0 0 0 0 rgba(215, 32, 39, 0);
	}
}
/*End animations*/

/*Start media queries*/
@media screen and (max-width: 768px) {
	body{
	background: url(/static/media/sitebg2.48aa7db1.jpg) no-repeat center fixed;
	background-size: cover;	
	}

	.landing-text h1{
		font-size: 15rem;
	}

	.landing-text h6{
		font-size: 1.7rem;
	}

	.nav-list{
		flex-direction: column;
	}

	li{
		margin: 2rem 0;
	}

	.nav-link{
		font-size: 4.5rem;
	}

	.about-heading h1{
		font-size: 8rem;
	}

	.section-heading h1{
		font-size: 3rem;
	}

	.about-details .nav-list{
		flex-direction: initial;
	}

	.about-details li{
		margin: 0 2rem;
	}

	.icon-container{
		width: 7rem;
		height: 7rem;
	}

	.icon-container i{
		font-size: 4rem;
	}

	.timeline ul{
		margin: 0 0 0 auto;
	}

	.timeline .date::before{
		left: -20rem;
	}

	.timeline .date::after{
		left: -5.9rem;
	}
}

@media screen and (max-width: 600px) {

	.menu-toggler{
		top: 2rem;
		right: 2rem;
	}

	.landing-text h1{
		font-size: 8rem;
	}

	.landing-text h6{
		font-size: 1.4rem;
	}

	.nav-link{
		font-size: 3.5rem;
	}

	.about .container{
		flex-direction: column;
	}

	.profile-img{
		margin: 0 0 7rem 0;
	}

	.portfolio-item{
		flex-direction: column;
	}

	.portfolio-img{
		margin: 0 0 2rem 0;
		order: 1;
	}

	.portfolio-description{
		margin: 0;
		order: 2;
	}

	.timeline p{
		font-size: 1.6rem;
	}

	.timeline .date::before{
		font-size: 1.4rem;
	}

	form{
		width: 100%;
	}

	.up{
		right: 2rem;
	}

}
/*End media queries*/

